.dash-page {
    min-height: 100vh;
    padding: 1.5rem 1.75rem;
    background: radial-gradient(circle at top, #0f172a 0, #020617 55%, #000 100%);
    display: flex;
    justify-content: center;
    align-items: stretch;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
}

.dash-blob {
    position: absolute;
    width: 500px;
    height: 500px;
    border-radius: 999px;
    filter: blur(80px);
    opacity: 0.6;
    z-index: 0;
    pointer-events: none;
    animation: dash-float 18s ease-in-out infinite alternate;
}

.dash-blob--1 {
    top: -120px;
    left: -80px;
    background: radial-gradient(circle, #2563eb, #0ea5e9);
}

.dash-blob--2 {
    bottom: -160px;
    right: -60px;
    background: radial-gradient(circle, #f97316, #e11d48);
    animation-delay: 2.5s;
}

@keyframes dash-float {
    0% {
        transform: translate3d(0, 0, 0) scale(1);
    }
    100% {
        transform: translate3d(24px, -18px, 0) scale(1.04);
    }
}

.dash-shell {
    position: relative;
    z-index: 1;
    width: 100%;
    max-width: 1200px;
    background: radial-gradient(circle at top left, rgba(148, 163, 184, 0.16), transparent 55%),
    rgba(15, 23, 42, 0.92);
    border-radius: 1.75rem;
    border: 1px solid rgba(148, 163, 184, 0.45);
    padding: 1.25rem 1.5rem 1.5rem;
    box-shadow:
            0 24px 70px rgba(15, 23, 42, 0.95),
            0 0 0 1px rgba(15, 23, 42, 0.9);
    backdrop-filter: blur(28px);
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}

.dash-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    padding-bottom: 0.75rem;
    border-bottom: 1px solid rgba(148, 163, 184, 0.35);
}

.dash-logo {
    display: flex;
    align-items: center;
    gap: 0.75rem;
}

.dash-logo-icon {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: radial-gradient(circle at 20% 0%, #f97316, #2563eb);
    box-shadow: 0 10px 28px rgba(15, 23, 42, 0.9);
    font-size: 1.5rem;
}

.dash-logo-text {
    display: flex;
    flex-direction: column;
}

.dash-logo-title {
    font-size: 1.1rem;
    font-weight: 700;
    color: #f9fafb;
}

.dash-logo-subtitle {
    font-size: 0.8rem;
    color: #9ca3af;
}

.dash-header-right {
    display: flex;
    align-items: stretch;
    gap: 1rem;
}

.dash-user-chip {
    display: flex;
    gap: 10px;
    align-items: center;
    background: rgba(15,23,42,.75) !important;
    border: 1px solid rgba(148,163,184,.35) !important;
    border-radius: 999px;
    padding: 8px 10px;
}

.dash-logout-btn {
    min-height: 44px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 999px;
    padding: 0 1.1rem;
    font-size: 0.8rem;
    font-weight: 600;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    cursor: pointer;
    background: rgba(15, 23, 42, 0.9);
    color: #e5e7eb;
    border: 1px solid rgba(239, 68, 68, 0.65);
    transition:
            background 0.15s ease,
            transform 0.1s ease,
            box-shadow 0.15s ease,
            border-color 0.15s ease;
}

.dash-user-avatar {
    width: 2rem;
    height: 2rem;
    border-radius: 999px;
    background: linear-gradient(135deg, #2563eb, #0ea5e9);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    color: #f9fafb;
}

.dash-user-info {
    display: flex;
    flex-direction: column;
    line-height: 1.1;
}

.dash-user-email {
    font-size: 0.8rem;
    color: #e5e7eb;
}

.dash-user-role {
    font-size: 0.7rem;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: #9ca3af;
}

.dash-logout-btn {
    border: none;
    border-radius: 999px;
    padding: 0.6rem 1.1rem;
    font-size: 0.8rem;
    font-weight: 600;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    cursor: pointer;
    background: rgba(15, 23, 42, 0.9);
    color: #e5e7eb;
    border: 1px solid rgba(148, 163, 184, 0.6);
    border-color: rgba(239, 68, 68, 0.95);
    transition:
            background 0.15s ease,
            transform 0.1s ease,
            box-shadow 0.15s ease;
}

.dash-logout-btn:hover {
    background: rgba(239, 68, 68, 0.18);
    border-color: rgba(239, 68, 68, 0.95);
    box-shadow: 0 10px 28px rgba(15, 23, 42, 0.9);
    transform: translateY(-0.5px);
}

.dash-logout-btn:active {
    transform: translateY(0);
    box-shadow: 0 6px 18px rgba(15, 23, 42, 0.95);
}

.dash-main {
    display: grid;
    grid-template-columns: minmax(0, 1.1fr) minmax(0, 1.3fr);
    gap: 1.25rem;
    margin-top: 1rem;
}

.dash-main-left {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.dash-card {
    background: rgba(15, 23, 42, 0.9);
    border-radius: 1.2rem;
    padding: 1.1rem 1.15rem;
    border: 1px solid rgba(148, 163, 184, 0.4);
    box-shadow: 0 14px 32px rgba(15, 23, 42, 0.95);
}

.dash-card--welcome h2 {
    margin: 0 0 0.4rem;
    font-size: 1.1rem;
    color: #f9fafb;
}

.dash-card--welcome p {
    margin: 0;
    font-size: 0.85rem;
    color: #d1d5db;
}

.dash-pill-list {
    list-style: none;
    padding: 0;
    margin: 0.75rem 0 0;
    display: flex;
    flex-wrap: wrap;
    gap: 0.4rem;
    font-size: 0.78rem;
    color: #9ca3af;
}

.dash-grid {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 0.75rem;
}

.dash-card--stat h3 {
    margin: 0 0 0.25rem;
    font-size: 0.9rem;
    color: #e5e7eb;
}

.dash-stat-number {
    margin: 0;
    font-size: 1.1rem;
    font-weight: 700;
    color: #f97316;
}

.dash-stat-caption {
    margin: 0.35rem 0 0;
    font-size: 0.78rem;
    color: #9ca3af;
}

.dash-main-right {
    display: flex;
    flex-direction: column;
}

@media (max-width: 960px) {
    .dash-main {
        grid-template-columns: minmax(0, 1fr);
    }
}

@media (max-width: 720px) {
    .dash-shell {
        padding: 1rem 1rem 1.25rem;
        border-radius: 1.25rem;
    }

    .dash-header {
        flex-direction: column;
        align-items: flex-start;
    }

    .dash-header-right {
        width: 100%;
        justify-content: space-between;
    }

    .dash-grid {
        grid-template-columns: repeat(2, minmax(0, 1fr));
    }
}

@media (max-width: 520px) {
    .dash-grid {
        grid-template-columns: minmax(0, 1fr);
    }
}

.dash-main--single {
    grid-template-columns: 1fr;
}

.dash-main--single .dash-main-right {
    min-width: 0;
    width: 100%;
}

.dash-main-below {
    grid-column: 1 / -1;
    min-width: 0;
    width: 100%;
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    gap: 12px;
}