.ledger-card {
    background: rgba(15, 23, 42, 0.95);
    border-radius: 1.2rem;
    padding: 1.1rem 1.15rem 1rem;
    border: 1px solid rgba(148, 163, 184, 0.4);
    box-shadow: 0 16px 36px rgba(15, 23, 42, 0.95);
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}

.ledger-header {
    display: flex;
    justify-content: space-between;
    gap: 0.75rem;
    align-items: flex-end;
}

.ledger-header h2 {
    margin: 0 0 0.2rem;
    font-size: 1rem;
    color: #f9fafb;
}

.ledger-header p {
    margin: 0;
    font-size: 0.8rem;
    color: #9ca3af;
}

.ledger-toolbar {
    display: flex;
    gap: 0.45rem;
    align-items: stretch;
    justify-content: flex-end;
    flex-wrap: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    padding-bottom: 2px;
    scrollbar-width: thin;
}

.ledger-toolbar > * {
    flex: 0 0 auto;
}

.ledger-input,
.ledger-btn {
    height: 36px;
    box-sizing: border-box;
}

.ledger-input {
    padding: 0 0.7rem;
    border-radius: 999px;
    border: 1px solid rgba(148, 163, 184, 0.5);
    background: rgba(15, 23, 42, 0.9);
    color: #e5e7eb;
    font-size: 0.8rem;
    outline: none;
    min-width: 180px;
}

.ledger-btn {
    border-radius: 999px;
    border: 1px solid rgba(148, 163, 184, 0.6);
    padding: 0 0.9rem;
    font-size: 0.8rem;
    cursor: pointer;
    background: transparent;
    color: #e5e7eb;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
}

.ledger-btn:disabled {
    opacity: 0.55;
    cursor: not-allowed;
}

.ledger-btn--ghost:hover {
    background: rgba(37, 99, 235, 0.25);
    border-color: rgba(59, 130, 246, 0.9);
}

.ledger-info { font-size: 0.85rem; color: #e5e7eb; }
.ledger-error { font-size: 0.85rem; color: #fecaca; }


.ledger-error--scroll {
    max-height: 220px;
    overflow: auto;
}

.ledger-error-pre {
    margin: 0;
    white-space: pre-wrap;
    word-break: break-word;
    font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    font-size: 12px;
    line-height: 1.35;
}

.ledger-tablewrap {
    border-radius: 0.9rem;
    border: 1px solid rgba(31, 41, 55, 0.9);
    overflow: hidden;
    background: radial-gradient(circle at top left, rgba(30, 64, 175, 0.15), transparent 60%),
    rgba(15, 23, 42, 0.95);
}

.ledger-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.8rem;
    color: #e5e7eb;
}

.ledger-table th, .ledger-table td {
    padding: 0.55rem 0.65rem;
    text-align: left;
    vertical-align: middle;
}

.ledger-table th {
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.08em;
    font-size: 0.68rem;
    color: #9ca3af;
    border-bottom: 1px solid rgba(55, 65, 81, 0.9);
}

.ledger-table tbody tr:nth-child(odd) { background: rgba(15, 23, 42, 0.98); }
.ledger-table tbody tr:nth-child(even) { background: rgba(17, 24, 39, 0.98); }
.ledger-table tbody tr:hover { background: rgba(30, 64, 175, 0.35); }

.ledger-empty { text-align: center; padding: 1rem 0.7rem; color: #9ca3af; }

.ledger-th-right, .ledger-td-right { text-align: right !important; }

.ledger-modal-backdrop{
    position: fixed;
    inset: 0;
    background: rgba(2, 6, 23, 0.72);
    backdrop-filter: blur(6px);
    -webkit-backdrop-filter: blur(6px);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 18px;
    z-index: 9999;
}

.ledger-modal{
    width: min(720px, 92vw);
    border-radius: 18px;
    padding: 18px 18px 16px;
    border: 1px solid rgba(148, 163, 184, 0.35);
    background:
            radial-gradient(circle at 20% 0%, rgba(59, 130, 246, 0.18), transparent 55%),
            radial-gradient(circle at 100% 20%, rgba(99, 102, 241, 0.14), transparent 55%),
            rgba(15, 23, 42, 0.96);
    box-shadow:
            0 24px 80px rgba(0, 0, 0, 0.55),
            0 10px 26px rgba(2, 6, 23, 0.55);
    max-height: 85vh;
}

.ledger-modal-head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 12px;
    margin-bottom: 14px;
}

.ledger-modal-head h3{
    margin: 0;
    font-size: 1.05rem;
    font-weight: 700;
    color: #f9fafb;
    letter-spacing: 0.01em;
}

.ledger-modal-head .ledger-btn--ghost{
    width: 40px;
    height: 40px;
    padding: 0;
    border-radius: 999px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.ledger-modal-body {
    overflow-y: auto;
    padding-right: 6px;
}

.ledger-label{
    display: grid;
    gap: 6px;
    font-size: 0.78rem;
    color: rgba(229, 231, 235, 0.9);
    margin-bottom: 12px;
}

.ledger-modal .ledger-input{
    height: 42px;
    border-radius: 999px;
    padding: 0 14px;
    border: 1px solid rgba(148, 163, 184, 0.45);
    background: rgba(2, 6, 23, 0.35);
    color: #e5e7eb;
    outline: none;
    box-shadow: inset 0 0 0 1px rgba(2, 6, 23, 0.15);
    transition: border-color 0.15s ease, box-shadow 0.15s ease, background 0.15s ease;
}

.ledger-modal .ledger-input:focus{
    border-color: rgba(59, 130, 246, 0.95);
    box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.18);
    background: rgba(2, 6, 23, 0.42);
}

.ledger-modal select.ledger-input{
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    padding-right: 44px;
    background-image:
            linear-gradient(45deg, transparent 50%, rgba(229, 231, 235, 0.9) 50%),
            linear-gradient(135deg, rgba(229, 231, 235, 0.9) 50%, transparent 50%);
    background-position: calc(100% - 18px) 55%, calc(100% - 12px) 55%;
    background-size: 6px 6px, 6px 6px;
    background-repeat: no-repeat;
}

.ledger-modal input[type="checkbox"]{
    width: 16px;
    height: 16px;
    accent-color: #3b82f6;
}

.ledger-modal .checks{
    display: flex;
    gap: 14px;
    align-items: center;
    flex-wrap: wrap;
    padding: 6px 2px 2px;
    margin-bottom: 10px;
}

.ledger-modal .checks label{
    display: inline-flex;
    gap: 8px;
    align-items: center;
    color: rgba(229, 231, 235, 0.9);
    font-size: 0.82rem;
}

.ledger-modal .ledger-btn--primary{
    height: 44px;
    border-radius: 999px;
    width: 100%;
    margin-top: 6px;
    font-weight: 700;
    letter-spacing: 0.01em;
    border: 1px solid rgba(59, 130, 246, 0.95);
    background: rgba(37, 99, 235, 0.25);
    transition: transform 0.08s ease, background 0.15s ease, box-shadow 0.15s ease;
}

.ledger-modal .ledger-btn--primary:hover{
    background: rgba(37, 99, 235, 0.95);
    transform: translateY(-1px);
    box-shadow: 0 10px 22px rgba(37, 99, 235, 0.18);
}

.ledger-modal .ledger-btn--primary:disabled{
    opacity: 0.55;
    transform: none;
    box-shadow: none;
}

.ledger-modal .ledger-error{
    border: 1px solid rgba(239, 68, 68, 0.45);
    background: rgba(239, 68, 68, 0.12);
    color: #fecaca;
    padding: 10px 12px;
    border-radius: 12px;
    margin-bottom: 12px;
}

@media (max-width: 520px){
    .ledger-modal{
        padding: 14px 14px 12px;
        border-radius: 16px;
    }
    .ledger-modal-head h3{
        font-size: 0.98rem;
    }
}

.ledger-hint {
    font-size: 0.78rem;
    color: rgba(229, 231, 235, 0.75);
}

.ledger-combo {
    position: absolute;
    top: calc(100% + 6px);
    left: 0;
    right: 0;
    z-index: 30;

    border-radius: 14px;
    border: 1px solid rgba(148, 163, 184, 0.35);
    background: rgba(15, 23, 42, 0.98);
    box-shadow: 0 18px 45px rgba(0, 0, 0, 0.45);

    max-height: 320px;
    overflow: auto;
    padding: 6px;
}

.ledger-combo-item {
    width: 100%;
    text-align: left;
    padding: 10px 12px;
    border-radius: 12px;
    border: 1px solid transparent;
    background: transparent;
    color: #e5e7eb;
    cursor: pointer;
    display: grid;
    gap: 2px;
}

.ledger-combo-item:hover {
    background: rgba(37, 99, 235, 0.18);
    border-color: rgba(59, 130, 246, 0.55);
}

.ledger-combo-item--all {
    font-weight: 700;
    background: rgba(148, 163, 184, 0.08);
    border-color: rgba(148, 163, 184, 0.18);
    margin-bottom: 6px;
}

.ledger-combo-name {
    font-size: 0.86rem;
    line-height: 1.2;
}

.ledger-combo-sub {
    font-size: 0.75rem;
    color: rgba(156, 163, 175, 0.95);
}

.ledger-combo-empty,
.ledger-combo-hint {
    padding: 10px 12px;
    color: rgba(156, 163, 175, 0.95);
    font-size: 0.8rem;
}

.ledger-btn--ghost.ledger-btn--active {
    background: rgba(37, 99, 235, 0.25);
    border-color: rgba(59, 130, 246, 0.9);
}

.ledger-btn--ghost.ledger-btn--active:hover {
    background: rgba(37, 99, 235, 0.25);
    border-color: rgba(59, 130, 246, 0.9);
}